import Image from "next/image";
import models from "../styles/models.module.scss"

interface ModelsClotchProps {
  title: string;
  imageOne: any;
  imageTwo: any;
  imageThree: any;
  imageFour: any;
}

export const ModelsСlotch = (props: ModelsClotchProps) => {
  return (
    <div>
      <div>
        <p>{props.title}</p>
      </div>
      <div>
        <Image
          src={props.imageOne}
          alt="ImageModels"
          className="models.modelsImage"
        />
        <Image src={props.imageTwo} alt="ImageModels" />
        <Image src={props.imageThree} alt="ImageModels" />
        <Image src={props.imageFour} alt="ImageModels" />
      </div>
    </div>
  );
};
